@import url("https://fonts.googleapis.com/css?family=Lato:400,700&display=swap");
/* Transition to showing the bigger shadow on hover */
.accessibility-open-cursor-workaround {
  display: none;
  pointer-events: none;
  position: absolute;
  width: 38px;
  height: 60px;
  z-index: 1000;
}

.preview-content {
  position: relative;
}
#preview-popup-button{
  display: none;
  transform: rotate(270deg) translateY(-50%);
  position: fixed;
  right: -100px;
  top: 50%;
  background-color: #574cd5;
  align-items: center;
  color: #fff;
  z-index: 9999;
  border: 0px;
  padding: 7px 15px;
  user-select: none;
  cursor: pointer;
}

#preview-popup-button span{
  padding-left: 10px;
  text-transform: uppercase;
  font-weight: 800;
  letter-spacing: 0.4px;
}
.accessibility-wrap {
  z-index: 1;
  user-select: none;
  box-sizing: initial;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
}
@media (max-width:1100px){
  #preview-popup-button{
    display: flex;
  }
  .accessibility-wrap {
    display: none;
    height: 100%;
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    right: 60px;
    z-index: 9999;
  }
  .accessibility-wrap.active{
    display: block;
  }
}
.accessibility-wrap .active-accessibility-item {
  background: #f0f0f0;
}
.accessibility-wrap * {
  box-sizing: initial;
  text-decoration: none;
}

.accessibility.menu-closed .accesibility-menu-btn {
  border-radius: 50%;
  transform: translateX(15px);
}

.accessibility-menu-btn {
  cursor: pointer;
  color: #028ef6;
  border: none;
  z-index: 1;
  box-shadow: 0 5px 15px 0 rgba(37, 44, 97, 0.15),
    0 2px 4px 0 rgba(93, 100, 148, 0.2);
  outline: none !important;
  transition: all 0.2s ease;
  border-radius: 50%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(0);
  position: relative;
}
.accessibility-menu-btn:hover {
  transform: scale(1.1, 1.1);
  opacity: 1 !important;
}
.accessibility-menu-btn:hover:after {
  opacity: 1;
}
.accessibility-menu-btn:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 100%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  opacity: 0;
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.accessibility-menu-btn svg {
  fill: #fff;
  height: 100%;
}

.accessibility-box {
  border-radius: 8px;
  box-shadow: -1px 0 20px -14px #000;
  opacity: 1;
  transition: 0.3s;
  z-index: 1000;
}
.accessibility-box .accessibility-box-heading {
  display: flex;
  align-items: center;
  position: relative;
  pointer-events: none;
}
.accessibility-box .accessibility-box-heading .accessibility-box-close {
  transition: opacity 0.3s ease-in-out;
}
.accessibility-box .accessibility-box-heading .accessibility-box-close:hover {
  opacity: 0.6;
}
.accessibility-box .accessibility-box-heading > p {
  padding: 0 0px 0 16px;
  color: #fff;
  margin: 16px 0;
  font-weight: 600;
  font-size: 13px !important;
  font-family: Lato, sans-serif !important;
}
.accessibility-box .accessibility-options {
  background: #fff;
  border-radius: 0 0 8px 8px;
}
.accessibility-box .accessibility-row {
  display: grid;
  grid-template-columns: 50% 50%;
  max-height: calc(100% - 67px - 36px);
  height: 100%;
}
.accessibility-box .accessibility-row .accessibility-col {
  cursor: pointer;
  border-bottom: 1px solid #c4c4c4;
}
.accessibility-box .accessibility-row .accessibility-col:hover {
  background-color: #f9f9f9;
}
.accessibility-box .accessibility-row .accessibility-col .center-text {
  text-decoration: none;
  text-align: center;
  color: #3d4246 !important;
  flex: 1 0 100%;
  margin-top: 6.4px;
  font-family: Lato, sans-serif !important;
  font-weight: 400 !important;
  font-size: 13px !important;
}
.accessibility-box .accessibility-row .accessibility-col > a[id] {
  text-decoration: none;
  color: #3d4246 !important;
  padding: 10.6px;
  display: block;
}
.accessibility-box .accessibility-row .accessibility-col > a[id] .option-svg {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.accessibility-box .accessibility-row .accessibility-col > a[id^="zoom"],
.accessibility-box .accessibility-row .accessibility-col a[id^="contrast"],
.accessibility-box .accessibility-row .accessibility-col a[id^="brightness"] {
  padding: 8px;
  display: block;
}
.accessibility-box-close {
  cursor: pointer;
  padding-right: 16px;
}
.accessibility-box-lines {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 9.6px;
}
.accessibility-box-lines > svg {
  margin: 0 4px;
}

.accessibility-reset {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  border-bottom: 1px solid #c4c4c4;
  max-height: 36px;
  pointer-events: none;
}
.accessibility-reset .center-text {
  padding-left: 8px;
  font-size: 13px !important;
  color: #3d4246 !important;
}

.accessibility-app-logo-container {
  position: relative;
  max-height: 67px;
}

.accessibility-app-logo {
  display: flex;
  align-self: center;
  justify-content: center;
  padding: 8px 0;
}

.accessibility-menu-closed .accessibility-box {
  opacity: 0;
  z-index: -100000;
  position: relative;
}

.accessibility-menu-closed .hide-overlay {
  height: 0;
  width: 0;
}

.accessibility-position-top-left {
  left: 15px;
  top: 15px;
}
.accessibility-position-top-left .accessibility-box {
  width: 318px;
  position: absolute;
  left: 80px;
}
.accessibility-position-top-left
