  .accessibility-box-heading
  p {
  padding: 0 0px 0 16px;
}

.accessibility-position-top-right {
  right: 15px;
  top: 15px;
}

.accessibility-position-bottom-right {
  right: 15px;
  align-items: flex-end;
  bottom: 15px;
}

.accessibility-position-bottom-left {
  left: 15px;
  align-items: flex-end;
  bottom: 15px;
}
.accessibility-position-bottom-left .accessibility-box {
  width: 318px;
  position: absolute;
  left: 80px;
}
.accessibility-position-bottom-left
  .accessibility-box
  .accessibility-box-heading
