  padding: 0 112px 0 16px;
}

.accessibility-position-middle-left {
  left: 15px;
  align-items: center;
  top: 50%;
}
.accessibility-position-middle-left .accessibility-box {
  width: 318px;
  position: absolute;
  left: 80px;
  margin-top: -2rem !important;
}
.accessibility-position-middle-left
