  padding: 0 112px 0 16px;
}

.accessibility-position-middle-right {
  right: 15px;
  align-items: center;
  height: 100%;
}

.accessibility-d-none {
  display: none;
}

.reading-guide {
  background: #000;
  width: 100% !important;
  min-width: 100% !important;
  top: 20px;
  box-sizing: border-box;
  height: 12px !important;
  border: solid 3px #fff300;
  border-radius: 5px;
  position: absolute;
  z-index: 99999;
}

.accesibility-widget-tooltip {
  position: absolute;
  border: 3px solid;
  background-color: #fff;
  padding: 16px 20px;
  font-size: 19px;
  border-radius: 5px;
  transition: all 70ms ease-out;
  opacity: 0;
}

.highlight-link {
  background-color: #000 !important;
  color: #f7ff00 !important;
}

.reader-active {
  background: #ff0 !important;
  color: #000 !important;
}

.accessibility-open-cursor:hover,
.accessibility-open-cursor * {
  cursor: url("data:image/png;base64,"),
    default !important;
}

.accessibility-open-cursor-ie:hover,
.accessibility-open-cursor-ie * {
  cursor: url(https://accessibly.apps.onthemapmarketing.com/public/images/ie-cursor.cur),
    auto !important;
}

.accessibility-position-middle-right.accessibility-menu-closed {
  right: 15px;
  align-items: center;
  top: 50%;
