  background: white;
  box-shadow: none;
  border: 2px solid #574cd5;
  border-radius: 10px;
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 100000;
  padding: 10px;
  height: auto;
  color: #000;
}
.accessibly-tab-popup p {
  margin: 0 10px;
  text-align: left;
}

a:focus {
  outline: -webkit-focus-ring-color auto 5px !important;
}

@media screen and (min-width: 768px) {
  .accessibly-fix {
    position: absolute !important;
    left: -22rem;
  }
  .accessibility-d-none-lg {
    display: flex;
  }
  .accessibility-d-none-sm {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .accessibility-d-none-lg {
    display: none;
  }
  .accessibility-d-none-sm {
    display: flex;
  }
}
